/* Properties Listing */
.property-listing-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  /* box-shadow: 0 0.2em 0.4em var(--text-color); */
  width: max(90%, 80%);
  margin: auto;
  gap: 1em;
  padding: 1em;
  border: 0.1em solid var(--dark-rgb);
  border-radius: 0.3em;
}

.property-head {
  text-align: center;
  padding: 1em;
}

.property-banner {
  position: relative;
  min-height: 15em;
}

.property-banner-text-container {
  text-align: center;
  padding: 1em;
  position: relative;
  top: 1rem;
  z-index: 1;
  color: var(--text-color);
  text-shadow: 0 0.2em 0.4em var(--background-color);
}

.property-banner-img-container {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
}

.property-banner-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  opacity: 0.7;
}

.property-banner-img:hover {
  opacity: 0.9;
}

.property-list {
  background-color: var(--text-color);
  color: var(--background-color);
  padding: 1em;
  border-radius: 0.3em;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.property-list:hover {
  transform: translateY(-0.3em);
  box-shadow: 0 0.2em 0.4em var(--text-color);
}

.property-img-container {
  width: 100%;
  height: 20em;
  position: relative;
}

.property-img {
  width: 100%;
  height: 100%;
  position: absolute;
  object-fit: cover;
  object-position: center;
}

.property-items {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  flex: 1;
}

.property-btn {
  background-color: var(--dark-black);
  color: var(--text-color);
  width: 100%;
  padding: 0.3em;
  border-radius: 0.1em;
  border: none;
  text-align: center;
  transition: letter-spacing, var(--slow-transition);
}

.property-btn:hover {
  letter-spacing: 0.1em;
}
