.contact-head {
  text-align: center;
  padding: 1em;
  background-color: var(--text-color);
  color: var(--background-color);
}

.contact-info {
  padding: 1em;
  text-align: center;
}

.error-message {
  text-align: center;
  width: max(90%, 80%);
  margin: auto;
  padding: 1em;
  font-size: var(--nav-font);
  box-shadow: 0 0.3em 0.5em var(--light-rgb);
  background-color: var(--text-color);
  color: var(--background-color);
}

.contact-form {
  padding: 1em;
  width: max(80%, 70%);
  margin: auto;
  box-shadow: 0.04em 0.08em 0.1em var(--light-rgb);
  border-radius: 0.3em;
  padding: 2.3em;
}

.name-container,
.email-container,
.message-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 1em;
}

.contact-form label {
  color: var(--text-color);
  font-size: var(--nav-font);
}

.contact-form input,
.contact-form textarea {
  padding: 1em;
  resize: none;
  outline: none;
  cursor: pointer;
  border-radius: 0.3em;
  border: none;
}

.contact-form input::placeholder,
.contact-form textarea::placeholder {
  transition: transform, var(--slow-transition) ease-in-out;
}

.contact-form input:hover::placeholder,
.contact-form textarea:hover::placeholder {
  transform: translateY(-0.5em);
}

.contact-form input:hover,
.contact-form textarea:hover {
  box-shadow: 0.1em 0.2em 0.4em var(--light-rgb);
}

.form-btn-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.form-btn {
  background-color: var(--dark-black);
  color: var(--text-color);
  padding: 0.5em;
  transition: letter-spacing,
    var(--normal-transition) cubic-bezier(0.755, 0.05, 0.855, 0.06);
  border-radius: 0.3em;
}

.form-btn:hover {
  letter-spacing: 0.1em;
}

.contact-bridge {
  padding: 2em;
}

/* aside */
.contact-img {
  width: max(80%, 70%);
  margin: auto;
  position: relative;
}

.contact-img img {
  object-fit: cover;
  object-position: center;
  width: 100%;
  max-height: 12em;
}

.contact-img .img-text {
  position: absolute;
  padding: 1em;
  text-shadow: 0.1em 0.3em 0.5em var(--dark-rgb);
}

.contact-details {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 2em;
  width: max(80%, 70%);
  margin: auto;
}

.contact-details > * {
  box-shadow: 0 0.4em 0.7em var(--light-rgb);
  padding: 2em;
  border-radius: 0.3em;
  background: linear-gradient(
    to right,
    var(--background-color) 70%,
    var(--text-color)
  );
}

.contact-phone,
.contact-whatsapp {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1em;
}

.contact-phone h4,
.contact-whatsapp h4 {
  transition: transform,
    var(--slow-transition) cubic-bezier(0.455, 0.03, 0.515, 0.955);
}

.contact-phone:hover h4,
.contact-whatsapp:hover h4 {
  transform: translateY(1rem);
}

.contact-phone small,
.contact-whatsapp small {
  margin: 0.5em 0;
  width: max(90%, 80%);
  text-align: center;
}

.contact-link {
  display: inline-block;
  background-color: var(--dark-black);
  color: var(--text-color);
  padding: 0.5em;
  width: max(90%, 80%);
  text-align: center;
  border-radius: 0.3em;
  transition: letter-spacing, var(--slow-transition) ease-in-out;
}

.contact-link:hover {
  letter-spacing: 0.1em;
}

.contact-bottom {
  padding: 1em;
  width: max(70%, 60%);
  margin: auto;
}
