/* Global Styling */
html {
  box-sizing: border-box;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-size: 62.5%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

button {
  cursor: pointer;
  font-family: inherit;
}

a {
  text-decoration: none;
}

:root {
  /* font */
  --nav-font: clamp(1rem, 1.7vw, 1.2rem);
  --text-font: clamp(1.2rem, 2vw, 1.6rem);
  --header-font: clamp(2.3rem, 3.5vw, 3.3rem);
  --subhead-font: clamp(1.6rem, 2.5vw, 2.4rem);
  --title-font: clamp(1.3rem, 2.2vw, 2rem);

  --ex-small-font: 0.7rem;
  --small-font: 1rem;
  --medium-font: 1.5rem;
  --large-font: 2rem;
  --ex-large-font: 2.5rem;

  /* color */
  --background-color: #36454f;
  --light-color: #a9a9a9;
  --text-color: #fffff0;
  --primary-black: #1113;
  --secondary-black: #1115;
  --dark-black: #111;

  /* rgb */
  --dark-rgb: rgb(25, 25, 112, 0.2);
  --light-rgb: rgb(255, 255, 255, 0.1);

  /* transiton */
  --fast-transition: 0.3s;
  --normal-transition: 0.6s;
  --slow-transition: 1s;
}

h1 {
  font-size: var(--header-font);
}

h2 {
  font-size: var(--subhead-font);
}

p {
  font-size: var(--text-font);
}

a {
  text-decoration: none;
}

a,
button {
  font-size: var(--nav-font);
  cursor: pointer;
}

li {
  list-style-type: none;
}

body {
  background: linear-gradient(var(--background-color), var(--background-color));
  background-repeat: no-repeat;
  color: var(--text-color);
}

section {
  padding: 1em;
}

header.header-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 2em;
  border-bottom: 0.1em solid var(--light-rgb);
  padding: 1em;
  position: sticky;
  top: -1em;
  left: 0;
  width: 100%;
  z-index: 4;
  background-color: var(--dark-black);
  padding: 1em;
  transition: background-color, color, box-shadow,
    var(--slow-transition) ease-in-out;
}

.onscroll {
  box-shadow: 0 0.5em 0.7em var(--light-rgb);
}

header.onscroll {
  background-color: var(--text-color);
  color: var(--background-color);
  box-shadow: 0 0.5em 0.7em var(--dark-rgb);
}

.onscroll .list-items .list-child a {
  color: var(--background-color);
}

.brand-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.brand-name {
  cursor: pointer;
  padding: 0 0.4em;
  transition: transform, var(--slow-transition) ease-in-out;
}

.brand-name:hover {
  transform: translate(-0.3em, 0.3em);
}

nav.nav-container {
  flex: 0.5;
  margin-top: 0.5em;
}

.nav-container ul.list-items {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  height: 100%;
}

.list-child a {
  color: var(--text-color);
  transition: transform,
    var(--slow-transition) cubic-bezier(0.39, 0.575, 0.565, 1);
}

.list-child a:hover {
  display: inline-block;
  transform: scale(1.1);
}

.nav-container .active {
  text-decoration: underline;
}

/* Property Filter */
ul.property-filter {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(115px, 1fr));
  width: max(80%, 70%);
  margin: auto;
  gap: 1em;
  margin-top: 0.5em;
  padding: 0.3em;
}

.property-filter li {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0.3em;
}

.property-filter li select {
  background-color: var(--background-color);
  color: var(--text-color);
  padding: 0.3em;
  border-radius: 0.2em;
  border: 0.1em solid var(--text-color);
}

.property-filter li select:hover {
  background-color: var(--text-color);
  color: var(--background-color);
}

.property-filter li:first-child {
  cursor: pointer;
}

/* Loading screen */
.loading-screen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  z-index: 3;
  color: var(--text-color);
  font-size: 1.5em;
}

.loading-spinner {
  border: 0.4em solid var(--background-color);
  border-top: 0.4em solid var(--text-color);
  border-radius: 50%;
  width: 3.125em;
  height: 3.125em;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  50% {
    border: 0.4em solid var(--text-color);
    border-top: 0.4em solid var(--background-color);
  }
  100% {
    transform: rotate(360deg);
  }
}

.loading-brand {
  animation: fadeInOut 2s infinite;
}

@keyframes fadeInOut {
  0%,
  100% {
    opacity: 0.5;
  }
  50% {
    opacity: 0.9;
  }
}

/* Modal */
.modal-container {
  background-color: #3339;
  position: fixed;
  z-index: 5;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}

.modal {
  position: fixed;
  width: max(70%, 50%);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--background-color);
  padding: 2em;
  box-shadow: 0 0.2em 0.5em rgba(0, 0, 0, 0.8);
  border-radius: 0.8em;
  text-align: center;
  transition: opacity 0.3s ease;
  z-index: 5;
}

.modal h3 {
  padding: 0.3em;
}

.modal input,
.modal textarea {
  width: 100%;
  margin-bottom: 1em;
  padding: 0.8em;
  border: 0.1em solid var(--text-color);
  border-radius: 0.3em;
  background-color: transparent;
  outline: none;
  color: var(--text-color);
  resize: none;
}

.modal textarea::-webkit-scrollbar {
  display: none;
}

.modal button {
  margin: 0.8em;
  padding: 1em 1.6em;
  border: none;
  border-radius: 0.4em;
  background-color: var(--text-color);
  color: var(--background-color);
  cursor: pointer;
  transition: background-color, color, border,
    var(--fast-transition) cubic-bezier(0.455, 0.03, 0.515, 0.955);
}

.modal button:hover {
  background-color: var(--background-color);
  border: 0.1em solid var(--text-color);
  color: var(--text-color);
}

/* Support-Aside */
.support-aside {
  padding: 1em;
  width: max(80%, 70%);
  margin: auto;
}

.support-title {
  text-align: center;
  margin-top: 1em;
}

.support-text-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.support-text {
  text-align: center;
}

.support-text-container span {
  font-size: var(--title-font);
  transition: transform,
    var(--slow-transition) cubic-bezier(0.455, 0.03, 0.515, 0.955);
  padding: 1em;
}

.support-text-container:hover span {
  transform: translateY(0.4em);
}

.support-details {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  align-items: center;
  gap: 0.3em;
  position: relative;
}

.support-overlay {
  position: absolute;
  top: 0;
  right: 60%;
  bottom: 0;
  left: 0;
  background: linear-gradient(
    to left,
    var(--background-color) 30%,
    var(--text-color)
  );
  z-index: -1;
  border-radius: 0.3em;
}

.support-img {
  border-radius: 50%;
  width: 100%;
  height: 100%;
  transform: translateX(1.3rem);
}

.support-team {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 0.3em;
  padding: 2em;
}

.support-team > * {
  margin-top: 1em;
  display: flex;
  justify-content: space-around;
  box-shadow: 0 0.4em 0.7em var(--light-rgb);
  padding: 0.4em;
  border-radius: 0.3em;
  background: linear-gradient(
    to right,
    var(--background-color) 70%,
    var(--text-color)
  );
  color: var(--text-color);
  transition: transform, var(--slow-transition) cubic-bezier(0.19, 1, 0.22, 1);
}

.support-team > *:hover {
  transform: scale(1.1);
}

/* Footer */
.footer-container {
  background-color: var(--text-color);
  color: var(--background-color);
}

.footer-row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  padding: 1em;
  gap: 1em;
  width: max(85%, 70%);
  margin: auto;
}

.footer-sub {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  gap: 1em;
  padding: 0.5em;
}

footer a {
  color: var(--background-color);
}

.footer-bottom {
  background-color: var(--dark-black);
  color: var(--text-color);
  padding: 1em;
}

footer li {
  font-size: var(--nav-font);
}

.footer-bottom a {
  color: inherit;
}

/* media-queries */
@media (min-width: 500px) {
  header.header-container {
    flex-direction: row;
    gap: unset;
    justify-content: space-around;
  }

  .brand-container {
    align-items: unset;
  }

  nav.nav-container {
    margin-top: unset;
  }

  .nav-container ul.list-items {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
