.property-details-head {
  text-align: center;
  color: var(--background-color);
  background-color: var(--text-color);
  padding: 0.4em;
}

.property-details-media {
  position: relative;
  width: 100%;
  height: 65vh;
}

.property-details-btn {
  display: flex;
  justify-content: space-between;
  position: absolute;
  top: 50%;
  width: 100%;
  z-index: 2;
  padding: 0.4em;
}

.property-details-btn > * {
  padding: 0.3em 0.6em;
  opacity: 0.8;
  background-color: var(--background-color);
  text-align: center;
  width: 3em;
  border: 0.2em solid var(--text-color);
  transition: opacity, color,
    var(--slow-transition) cubic-bezier(0.39, 0.575, 0.565, 1);
  border-radius: 0.3em;
}

.property-details-btn button:last-of-type {
  float: right;
}

.property-details-btn > *:hover {
  opacity: 1;
  color: var(--text-color);
}

.property-details-img-container {
  position: absolute;
  top: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  border: 0.1em solid var(--light-rgb);
}

.property-details-img {
  position: absolute;
  top: 0;
  bottom: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center;
  opacity: 0.7;
  transition: opacity,
    var(--slow-transition) cubic-bezier(0.39, 0.575, 0.565, 1);
}

.anchor {
  position: absolute;
  left: 50%;
  padding: 0.3em;
  transform: translateX(-50%);
  bottom: 0;
  z-index: 1;
}

.anchor > * {
  font-size: var(--header-font);
  color: var(--text-color);
  opacity: 0.6;
  border-radius: 0.5em;
  cursor: pointer;
  padding: 0.15em;
  transition: opacity, var(--fast-transition) ease-in-out;
}

.active-anchor {
  opacity: 1;
  color: var(--background-color);
}

.property-details-img:hover,
.property-details-btn > *:hover,
.anchor > *:hover {
  opacity: 1;
}

.property-details-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em;
  width: max(90%, 80%);
  margin: auto;
  border-bottom: 0.1em solid var(--light-rgb);
}

.property-details-list > * {
  font-size: var(--nav-font);
}

.property-details-info {
  padding: 1em;
}

.property-details-description {
  font-size: var(--nav-font);
  padding: 1em;
}

.property-features {
  padding: 1em;
}

.details-btn {
  display: flex;
  margin-left: auto;
  margin-right: auto;
  background-color: var(--dark-black);
  color: var(--text-color);
  width: fit-content;
  padding: 0.7em;
  border-radius: 0.1em;
  border: none;
  text-align: center;
  transition: letter-spacing, var(--slow-transition);
}

.property-features ul {
  display: grid;
  grid-template-columns: 1fr;
  padding: 0.5em;
}
