/* Hero-Section */
.hero-section {
  padding: 1em;
}

.hero-container {
  position: relative;
  width: 100%;
  height: 60vh;
}

.hero-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.hero-image-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.hero-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  transition: filter, var(--normal-transition) cubic-bezier(0.23, 1, 0.32, 1);
}

.hero-description-container,
.hero-btn-container,
.hero-title-container {
  position: relative;
  z-index: 1;
}

.hero-sub-container {
  padding: 1em;
  position: absolute;
  top: 40%;
  width: max(80%, 70%);
  margin: auto;
  text-shadow: 0.2em 0.5em 0.7em var(--background-color);
}

.hero-title-container {
  text-align: center;
}

.hero-title {
  animation: right-fade-in 1s linear;
}

@keyframes right-fade-in {
  from {
    opacity: 0.2;
    transform: translateX(100%);
  }
  to {
    opacity: 1;
    transform: translateX(0%);
  }
}

.hero-description-container {
  padding: 1em;
}

.hero-description {
  animation: left-fade-in 1s linear;
  text-align: center;
}

@keyframes left-fade-in {
  from {
    opacity: 0.2;
    transform: translateX(-100%);
  }
  to {
    opacity: 1;
    transform: translateX(0%);
  }
}

.hero-btn-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  top: 50%;
}

.hero-btn {
  padding: 0.7em;
  opacity: 0.4;
  transition: opacity, border,
    var(--slow-transition) cubic-bezier(0.39, 0.575, 0.565, 1);
  background-color: var(--background-color);
  color: var(--text-color);
  width: 3em;
  position: absolute;
  top: 50%;
}

.next-btn {
  right: 0;
}

.hero-btn:hover {
  opacity: 0.89;
  border: 0.1em solid var(--text-color);
}

/* filter */
.filter-container {
  width: max(90%, 70%);
  margin: auto;
  background-color: var(--light-color);
  padding: 0.5em;
  box-shadow: 0 0.3em 0.5em var(--dark-rgb);
  transform: translateY(-4em);
}

.filter-head {
  padding: 0.5em;
}

.property-section a {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  gap: 0.6em;
  width: 15em;
  border: 0.1em solid var(--text-color);
  padding: 0.3em;
  border-radius: 0.2em;
  color: var(--text-color);
  transition: background-color, color,
    var(--normal-transition) cubic-bezier(0.445, 0.05, 0.55, 0.95);
  margin-top: 0.5em;
}

.property-section a:hover {
  background-color: var(--text-color);
  color: var(--background-color);
}

.property-section a span {
  transition: transform,
    var(--fast-transition) cubic-bezier(0.445, 0.05, 0.55, 0.95);
}
.property-section a:hover span {
  transform: translateX(0.3em);
}

/* Founder Section */
.founder-details {
  box-shadow: 0.5em 0.5em 0.7em var(--dark-rgb);
  padding: 1em;
  width: max(80%, 70%);
  margin: auto;
  display: grid;
  gap: 1.5em;
  border-radius: 0.3em;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  place-items: center;
}

.founder-about-container h4 {
  border-bottom: 0.1em solid var(--text-color);
  padding: 0.3em;
}

.founder-about-container span {
  padding: 0.3em;
  text-shadow: 0 0.1em 0.2em var(--text-color);
}

.founder-img {
  width: 100%;
  box-shadow: 0 0.2em 0.3em var(--text-color);
  border-radius: 0.1em;
}

.founder-details a {
  padding: 0.2em;
  background-color: var(--text-color);
  color: var(--background-color);
  margin-top: 0.1em;
  float: right;
  border-radius: 0.1em;
  transition: letter-spacing, var(--fast-transition) ease-in-out;
}

.founder-details a:hover {
  letter-spacing: 0.03em;
}

/* Testimonial */
aside.testimonial-aside {
  padding: 1em;
}

.testimonial-head {
  text-align: center;
  margin-top: 1em;
}

.testimonial-title {
  text-align: center;
}

.testimonial-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 0.5em;
  width: max(80%, 75%);
  margin: auto;
}

.testimonial-details {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 0.3em;
  height: 100%;
  box-shadow: 0.5em 0.5em 0.7em var(--dark-rgb);
  padding: 1em;
  background: linear-gradient(
    to top,
    var(--background-color) 70%,
    var(--text-color)
  );
  transition: transform,
    var(--fast-transition) cubic-bezier(0.6, 0.04, 0.98, 0.335);
  border-radius: 0.3em;
}

.testimonial-details:hover {
  transform: scale(1.05);
}

.testimonial-name {
  display: block;
  text-align: center;
  background-color: var(--text-color);
  color: var(--background-color);
  padding: 0.3em;
}

.testimonial-img-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5em;
}

.testimonial-img-container img {
  width: 3em;
  height: 3em;
  border-radius: 50%;
}
