.about-section {
  animation: fade-in 1s ease-in-out;
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.about-head {
  background-color: var(--text-color);
  color: var(--background-color);
  text-align: center;
  padding: 1em;
}

.about-title {
  text-align: center;
  padding: 1em;
}

.about-content {
  padding: 1em;
  animation: slide-in 1s ease-in-out;
  width: max(90%, 80%);
  margin: auto;
}

@keyframes slide-in {
  0% {
    transform: translateX(-50px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

.about-details-container {
  padding: 1em;
  min-height: auto;
}

.about-details {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  place-items: center;
  justify-content: space-around;
  padding: 1em;
  gap: 1em;
  box-shadow: 0 0.3em 0.5em var(--text-color);
  min-height: auto;
  width: max(90%, 80%);
  margin: auto;
}

.about-title {
  text-align: center;
  padding: 1em;
}

.about-text {
  border: 0.1rem solid var(--light-rgb);
  padding: 1rem;
}

.about-img-container {
  box-shadow: 0 0.2em 0.5em var(--light-rgb);
  transform: translateY(2rem);
  height: 20em;
}

.about-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  transition: transform,
    var(--slow-transition) cubic-bezier(0.455, 0.03, 0.515, 0.955);
}

.about-img:hover {
  transform: translateY(-2rem);
}
