.not-found-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70vh;
}

.not-found-content {
  text-align: center;
  animation: slide-in 0.6s linear;
}

@keyframes slide-in {
  0% {
    opacity: 0;
    transform: translateY(-2em);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.not-found-content h2 {
  color: #e74c3c;
  font-size: 8em;
  animation: slideIn 1s ease-in-out;
}

.back-link {
  color: inherit;
  text-decoration: none;
  font-weight: bold;
  transition: text-decoration, text-decoration-color,
    var(--slow-transition) ease-in-out;
}

.back-link:hover {
  text-decoration: underline;
}

@keyframes slideIn {
  0% {
    transform: translateX(-5em);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
